@import '../../../style/mixins.scss';
@import '../../../style/variables.scss';

.card-briefcase{
    width: 300px;
    margin: 10px;
    height: 300px;
    padding: 10px;
    display: flex;
    cursor: pointer;
    border-radius: 10px;
    align-items: center;
    flex-direction: column;
    color: $color-text-grey;
    background-color: $color-black-2;
    &:hover{
        background-color: #282828;
        &__description-tools{
            opacity: 1;
        }
        .card-briefcase__description-tools{
            animation-name: animationCardImage;
            animation-timing-function: linear;
            animation-fill-mode: forwards;
            animation-duration: 1s;
            @keyframes animationCardImage {
                100% {
                    top: 0px;
                    opacity: 0.5;
                    background-color: #715ebc;
                }
              }

        }
    }
    &__content-image{
        position: relative;
        border-radius: 10px;
        overflow: hidden;
    }
    &__description-tools{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        top: 140px;
        position: absolute;
        opacity: 1;
        border-radius: 10px;
        & img{
            opacity: 1;
            margin: 5px 10px;
        }
    }
    &__image{
        width: 100%;
        height: 100%;
        border-radius: 10px;
    }
}
